/* CustomersList.css */

.customers-list {
  font-family: Arial, sans-serif;
  margin: 20px;
  text-align: center;
}

h2 {
  text-align: center;
  color: #333;
}

.filter {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.filter select,
.filter input {
  padding: 10px;
  margin: 0 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

table, th, td {
  border: 1px solid #ddd;
}

th {
  background-color: #4CAF50;
  color: white;
  padding: 15px;
  text-align: center;
}

td {
  padding: 15px;
  font-weight: bolder;
  text-align: center;
  transition: background-color 0.3s;
}

td:hover {
  background-color: #f1f1f1;
}

tr:nth-child(even) {
  background-color: #f9f9f9;
}

tr:nth-child(odd) {
  background-color: #fff;
}

tr:hover {
  background-color: #f1f1f1;
}

@media (max-width: 768px) {
  .filter {
    flex-direction: column;
  }

  .filter select,
  .filter input {
    width: 100%;
    margin-bottom: 10px;
  }

  th, td {
    padding: 10px;
    font-size: 14px;
  }
}
